


























import { Component, Prop, Vue } from "vue-property-decorator";
import axios from "axios";

@Component
export default class DominanceBar extends Vue {
  barWidth: number = 0;
  barMarkerPos: number = 0;

  created() {
    axios
      .get("https://api.blockchain.info/pools?cors=true&timespan=4days")
      .then(response => {
        var grouped = this.group(response.data);
        this.barWidth = Math.round(grouped.bitmain / (grouped.bitmain + grouped.other) * 1000) / 10;
      });

    axios
      .get("https://api.blockchain.info/pools?cors=true&timespan=24hours")
      .then(response => {
        var grouped = this.group(response.data);
        this.barMarkerPos = Math.round(grouped.bitmain / (grouped.bitmain + grouped.other) * 1000) / 10;
      })
  }

  private group(data: any) {
    var grouped = { bitmain: 0, other: 0 };

    for (var prop in data) {
      switch(prop) {
        case "ViaBTC":
        case "BTC.com":
        case "AntPool":
        case "BTC.TOP":
        case "Bitcoin.com":
          grouped.bitmain += data[prop];
          break;
        default:
          grouped.other += data[prop];
          break;
      }
    }

    return grouped;
  }
}



